/* Declaring variables */

$georgia: Georgia, serif;
$graphik: Graphik, helvetica, arial, sans-serif;

$gbody-width: 600px;
$gbody-padding: 20px;

$navbar-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.27);
$navbar-height: 48px;

$fade-transition-duration: 0.25s;

