  color: #121212;
}

a {
  color: #337ab7;
  text-decoration: none;

  &[href^="mailto:"] {
    word-wrap: break-word;
  }
}

a:hover {
  color: #23527c;
  text-decoration: underline;
}

b {
  font-weight: 700;
}

.g-body {
  max-width: $gbody-width;
  width: calc(100% - #{2 * $gbody-padding});
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
}

.paragraph {
