/* Navbar */

#navbar {
  display: flex;
  width: 100%;
  height: $navbar-height;
  position: fixed;
  background-color: #fff;
  z-index: 1000;
  transition-duration: $fade-transition-duration;
  box-shadow: $navbar-shadow;
}

#navbar.only-eye-logo {
  background: none;
  box-shadow: none;

  #nav-links {
    opacity: 0;
  }

  .hamburger-btn {
    opacity: 0;
    visibility: hidden;
  }
}

#eye-logo-container {
  display: flex;
  justify-content: space-between;
  height: 48px;
  z-index: 1;

  #eye-logo {
    width: 96px;
  }
}

#navbar.news-navbar {
  height: 46px;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.27);
}

#navbar.news-navbar.hide-news-navbar {
  height: 46px;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.27);
  opacity: 0;
}

#cds-logo-container {
  display: block;
  padding: 8px 0;
  margin: 0 auto;
  align-self: center;

  #cds-logo {
    height: 19px;
  }
}

.hamburger-btn {
  align-self: center;
  height: 18px;
  border: none;
  cursor: pointer;
  width: 18px;
  padding: 0;
  margin-right: 20px;
  display: none;
  background: none;
}

#navbar:not(.show-nav-links) {
  .hamburger,
  .hamburger-btn:after,
  .hamburger-btn:before {
    background-color: #111;
    height: 2px;
    margin-bottom: 4px;
    width: 100%;
  }

  .hamburger-btn:after {
    content: "";
    display: block;
    margin-bottom: 0;
  }

  .hamburger-btn:before {
    content: "";
    display: block;
  }
}

#nav-links {
  flex: 1 1;
  display: flex;
  align-items: center;
  transition-duration: $fade-transition-duration;

  a {
    margin: 0;
    margin-left: 42px;
    font-family: $graphik;
    font-size: 14px;
    color: #121212;
    text-decoration: none;
  }

  a:hover {
    text-decoration: underline;
  }
}

// Collapse navbar
@media (max-width: 760px) {
  #navbar {
    flex-direction: column;

    &.news-navbar {
      justify-content: center;
    }
  }

  #nav-links {
    visibility: hidden;
    opacity: 0;
    flex-direction: column;

    a {
      width: 100%;
      text-align: center;
      background: #fff;
      padding: 17px 0;
      margin-left: 0;
    }
  }

  #navbar.show-nav-links {
    .hamburger-btn {
      background: url("data:image/svg+xml;base64,")
        no-repeat 50% content-box;
      background-size: 86% auto;
    }

    #navbar {
      box-shadow: none;
    }

    #nav-links {
      visibility: visible;
      opacity: 1;
      box-shadow: 0 -10px white,
        // White shadow to mask top box-shadow
          $navbar-shadow;
      flex: 1 0 auto;
    }

    #eye-logo-container {
      border-bottom: 1px solid #ddd;
      flex: 1 0 auto;
    }
  }

  #navbar.show-nav-links.only-eye-logo #eye-logo-container {
    border: none;
  }

  #eye-logo {
    width: 72px;
  }

  .hamburger-btn {
    display: block;
  }
}

